@import "../../scss/_sass_vars.scss";

@keyframes headerAnimation {
  0% {
    opacity: 0.1;
    transform: translateX(10%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

.hero {
  overflow: visible;
}

.contentBlock {
  width: 65%;
  padding: 35px 0;
  animation: headerAnimation $animationSettings;
  animation-fill-mode: forwards;

  @media screen and(max-width: $mobileBreakpoint ) {
    width: 100%;
  }

}

.header {
  margin-bottom: 70px;
  h1 {
    margin-bottom: 20px;
    font-size: 45px;
  }

  p {
    font-size: 22px;
    padding-left: 10px;
  }
}

.tilesWrapper {
  display: flex;
  gap: 10px;
  list-style: none;

  @media screen and(max-width: $mobileBreakpoint ) {
    flex-direction: column;
  }

  li {
    height: 150px;
    position: relative;
    flex-grow: 1;
    padding: 10px;
    border-radius: 10px;
    background-color: var(--main-background-color-alpha);
    backdrop-filter: blur(2px);
    overflow: hidden;
    box-shadow: 0px 0px 7px 1px black inset;
    cursor: pointer;
    user-select: none;
  }

  .tileIcon {
    position: absolute;
    top: -10px;
    right: -15px;
    color: var(--main-text-color-alpha);
    pointer-events: none;
    z-index: -1;
  }

  .tileHeader {
    font-size: 20px;
    margin-bottom: 20px;
  }
}
